/* General Container Styling */
.weather-container {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 100%;
  margin: 0;
  background-color: transparent;
}

/* Forecast Buttons */
.forecast-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.forecast-buttons button {
  background-color: #2c3e50;
  color: #ffffff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.forecast-buttons button:hover {
  background-color: #1a252f;
}

.forecast-buttons button:focus {
  outline: none;
  background-color: #34495e;
}

/* Horizontal Forecast List */
.forecast-list {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding: 20px;
  scroll-snap-type: x mandatory;
  position: relative;
}

/* Custom Scrollbar Styling */
.forecast-list::-webkit-scrollbar {
  height: 8px;
}

.forecast-list::-webkit-scrollbar-thumb {
  background-color: #555;
  border-radius: 4px;
  border: 2px solid #2c3e50;
}

.forecast-list::-webkit-scrollbar-track {
  background-color: #1c2833;
}

/* Forecast Item Styling */
.forecast-item {
  background-color: rgba(51, 51, 51, 0.8);
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5);
  padding: 20px;
  min-width: 200px;
  text-align: center;
  color: #e0e6ed;
  transition: transform 0.2s, box-shadow 0.2s;
  scroll-snap-align: start;
  cursor: pointer;
}

.forecast-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.5);
}

/* Icon Styling with Soft Glow */
.icon-container {
  font-size: 2.5em;
  color: #f0e68c;
  filter: drop-shadow(0px 0px 6px rgba(240, 230, 140, 0.5));
  margin-bottom: 10px;
}

/* Temperature and Condition Text */
.temperature {
  font-size: 1.5em;
  color: #ffa07a;
}

.condition {
  font-size: 1.2em;
  color: #87cefa;
}

/* Weather Detail Text */
.wind, .precipitation, .humidity, .details {
  color: #d3d3d3;
  font-size: 0.9em;
}

/* Alerts Section Styling */
.alerts-section {
  background-color: rgba(255, 69, 58, 0.2);
  border-left: 4px solid #ff4444;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
  color: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
}

.alert-title {
  font-size: 1.5em;
  color: #ff4444;
  text-align: left;
  margin-bottom: 10px;
  font-weight: bold;
}

.alert-item {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid #ffdddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 10px;
}

.alert-item p {
  margin: 5px 0;
  font-size: 14px;
  color: #f8d7da;
}

/* Satellite View Section Styling */
.satellite-view {
  background-color: rgba(0, 0, 0, 0.3);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
  margin: 20px 0;
}

.satellite-view h2 {
  font-size: 1.5em;
  color: #ffffff;
  margin-bottom: 15px;
  text-align: left;
  font-weight: bold;
  text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

/* Satellite Image Styling */
.satellite-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
  margin-top: 10px;
}
/* Air Quality Section Styling */
.air-quality {
  border-radius: 8px;
  padding: 20px;
  margin: 20px 0;
  color: white;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
}

.air-quality h2 {
  font-size: 1.5em;
  margin-bottom: 15px;
  font-weight: bold;
}

.air-quality p {
  margin: 5px 0;
  font-size: 14px;
}

/* Colors based on AQI */
.air-quality.good {
  background-color: #4caf50; /* Green for Good */
}

.air-quality.moderate {
  background-color: #ffeb3b; /* Yellow for Moderate */
}

.air-quality.unhealthy-sensitive {
  background-color: #ff9800; /* Orange for Unhealthy for Sensitive Groups */
}

.air-quality.unhealthy {
  background-color: #f44336; /* Red for Unhealthy */
}

.air-quality.very-unhealthy {
  background-color: #9c27b0; /* Purple for Very Unhealthy */
}

.air-quality.hazardous {
  background-color: #d32f2f; /* Maroon for Hazardous */
}
